.img-position-center {
  position: relative;
  overflow: hidden;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover; } }

.scrollbar {
  // 
  &::-webkit-scrollbar {
    width: 2px;
    border-radius: 4px; }

  // 
  &::-webkit-scrollbar-thumb {
    border: solid 1px $secondary; }

  // 
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 3px 9px $light; } }
